import * as React from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import Layout from '../components/layout';

const GreetingPage = () => {
  return (
    <Layout
      pageTitle="ごあいさつ"
      pageDescription="こんにちは。ツキノラボは、月の森デザインの Web研究室です。ホームページ制作に関する情報を掲載しています。"
    >
      <StaticImage
        alt="こんにちは、月の森デザインです。"
        src="../img/page-greeting.png"
        placeholder="Blurred"
      />
      <p>ツキノラボは、<a href="https://moon-forest-design.com/" title="ホームページ制作の「月の森デザイン」" target="_blank" rel="noopener noreferrer">月の森デザイン</a>の Web研究室です。ホームページ制作に関する以下ようの情報を残してしておくために運営しています。</p>
      <ul>
        <li>技術メモ</li>
        <li>デモページ</li>
        <li>その他、制作物</li>
      </ul>
    </Layout>
  )
};

export default GreetingPage;
